  color: #fff;
  letter-spacing: 0.025rem;
}

.main-visual {
  overflow: hidden;
}

.main-visual__img {
  position: relative;
}
.main-visual__img:before {
  content: "";
  background: url(../../images/Sa.svg) no-repeat;
  background-size: contain;
  width: 19.0625rem;
  height: 15rem;
  position: absolute;
  right: -2.75rem;
  bottom: -1.6875rem;
}
@media screen and (min-width: 768px) {
  .main-visual__img:before {
    background: url(../../images/Sa-pc.svg) no-repeat;
    background-size: contain;
    width: 28.75rem;
    height: 22.375rem;
    bottom: -3.875rem;
    right: -3.1875rem;
  }
}

.drawer {
  position: fixed;
  top: 1.75rem;
  right: 1.25rem;
  width: 1.5625rem;
  height: 1.3125rem;
  z-index: 100;
}

.drawer__icon {
  position: relative;
  top: 0rem;
  width: 1.5625rem;
  height: 1.3125rem;
}
.drawer__icon.is-active {
  top: 0.4375rem;
}
.drawer__icon.is-active .drawer__bar1 {
  top: 0px;
  transition: transform 0.3s ease 0s;
  transform: rotate(-45deg);
}
.drawer__icon.is-active .drawer__bar2 {
  display: none;
}
.drawer__icon.is-active .drawer__bar3 {
  top: 0px;
  transition: transform 0.3s ease 0s;
  transform: rotate(45deg);
}

.drawer__bar1,
.drawer__bar2,
.drawer__bar3 {
  position: absolute;
  width: 1.5625rem;
  height: 0.1875rem;
  background: #fff;
  border-radius: 1.25rem;
  transition: transform 0.3s ease 0s;
}

.drawer__bar1 {
  top: 0;
}

.drawer__bar2 {
  top: 0.5625rem;
}

.drawer__bar3 {
  top: 1.125rem;
}

.drawer-content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(51, 51, 51);
  z-index: 20;
  display: none;
}

.drawer-content__items {
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.drawer-content__item {
  width: 12.5rem;
}
.drawer-content__item a {
  padding: 16px;
  line-height: 1;
  text-decoration: none;
  color: #fff;
  display: block;
  font-weight: 700;
  position: relative;
}

